const CustomCard = () => {
    return (
        <Grid.Container justify="center" id="NewItem">
            <Spacer y={3} />
            <Grid xs={12} sm={7}>
                <Card cover css={{
                    w: '100%',
                    p: 0

                }}>
                    <Card.Body>
                        <Card.Image
                            src='/essence.bunrumz/images/sm64gameplay2.jpg'
                            height={300}
                            width="100%"
                            alt="Relaxing app background"
                        />
                    </Card.Body>

                    <Card.Footer
                        blur
                        css={{
                            position: 'absolute',
                            bgBlur: '#0f1114',
                            borderTop: '$borderWeights$light solid $gray700',
                            bottom: 0,
                            zIndex: 1
                        }}
                    >
                        <Row>
                            <Col>
                                <Row>
                                    <Col>
                                        <Text color="#d1d1d1" size={12}>
                                            Recently Added
                                        </Text>
                                        <Spacer y={.4} />
                                        <Text color="#d1d1d1" size={16}>
                                            Super Mario 64
                                        </Text>
                                    </Col>
                                </Row>
                            </Col>
                            <Col>
                                <Row justify="flex-end">
                                    <Button auto css={{ color: '#94f9f0', bg: '#000000' }}>
                                        {<Play fill="currentColor" filled />}
                                        <Spacer x={0.2} />
                                        Play
                                    </Button>
                                </Row>
                            </Col>
                        </Row>
                    </Card.Footer>

                </Card>
            </Grid>
        </Grid.Container>
    )
}


export default function Games() {
    const navigate = useNavigate();
    // const location = useLocation();

    var request = new XMLHttpRequest();
    request.open("GET", "/essence.bunrumz/ListOfGames.json", false);
    request.send(null)
    console.log(request.responseText)
    var GameList = JSON.parse(request.responseText)

    return (
        <NextUIProvider theme={darkTheme}>
            <div style={{
                'background-image': 'url(/essence.bunrumz/sitefiles/bg3.jpg)',
                // 'filter': 'blur(8px)',
                'background-repeat': 'no-repeat',
                'background-attachment': 'fixed',
                'background-size': 'cover',
                width: '100vw',
                height: '100vh'
            }}>
                {/* <Spacer y={3} /> */}
                <Text h1
                    css={{
                        textGradient: " $black500 100",
                    }}
                >"aa"</Text>
                <CustomCard></CustomCard>
                <Spacer y={1} />
                <Grid.Container gap={2} justify="center">
                    {
                        Object.entries(GameList).map(([Game], i) => (
                            <Grid xs={10000} sm={2} key={i}>
                                <Image
                                    id={i}
                                    width='100%'
                                    height={100}
                                    src={GameList[Game]["CoverArt"]}
                                    alt="Default Image"
                                    objectFit="cover"
                                    onMouseEnter={
                                        () => {
                                            document.getElementById(i).style.cursor = 'pointer';
                                        }
                                    }
                                    onClick={() => {
                                        navigate(`/essence.bunrumz/Player?game=${Game}`)
                                    }}
                                    css={{
                                        boxShadow: '$md',
                                        position: 'relative',
                                        overflow: 'hidden',
                                        transition: 'all 0.4s ease',
                                        '&:hover': {
                                            transform: 'scale(1.05, 1.05)',
                                            'transform-origin': 'left right',
                                            zIndex: '1',
                                            transition: 'all 0.4s ease'
                                        }
                                    }}
                                />
                                {/* <Card hoverable animated shadow id={i}
                                    onMouseEnter={
                                        () => {
                                            document.getElementById(i).style.cursor = 'pointer';
                                        }
                                    }
                                    onClick={() => {
                                        navigate(`/essence.bunrumz/Player?game=${Game}`)
                                    }}
                                    css={{
                                        fontWeight: '$bold',
                                        boxShadow: '$md',
                                        position: 'relative',
                                        overflow: 'hidden',
                                        transition: 'all 0.4s ease',
                                        '&:hover': {
                                            transform: 'scale(1.05, 1.05)',
                                            'transform-origin': 'left right',
                                            zIndex: '1',
                                            transition: 'all 0.4s ease'
                                        }
                                    }}>
                                    <Card.Body css={{ p: 0 }}>
                                        <Card.Image
                                            objectFit="cover"
                                            src={GameList[Game]["CoverArt"]}
                                            width='100%'
                                            height={140}
                                            alt={GameList[Game]["CoverArt"]}
                                        />
                                    </Card.Body>
                                    {/* <Card.Footer justify="flex-end" blur="true">
                                    <Row wrap='wrap' justify="space-between">
                                        <Text b>
                                            {Game}
                                        </Text>
                                    </Row>
                                    <Row>
                                        <Col>
                                            <Row justify="flex-end">
                                                <Button flat auto rounded color="success" onClick={() => { navigate(`/essence.bunrumz/Player?game=${Game}`) }}>
                                                    <Text css={{ color: 'inherit' }} size={12} weight="bold" transform="uppercase">
                                                        Play
                                                    </Text>
                                                </Button>
                                            </Row>
                                        </Col>
                                    </Row>
                                </Card.Footer> 
                                </Card> */ }
                            </Grid>
                        ))
                    }
                </Grid.Container >
            </div>
        </NextUIProvider >
    );
}
